body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  background: rgb(9,9,121);
  background: linear-gradient(90deg, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

@font-face {
    font-family: 'Parquetipo Unicase';
    font-style: normal;
    font-weight: normal;
    src: local('Parquetipo Unicase'), url(/beta-touhou-friends/static/media/Parquetipo-Unicase.effea683.woff) format('woff');
    }
h1{
    font-family: 'Parquetipo Unicase';
    font-weight: 200;
    background: linear-gradient(to right, #00ff5e 0%, #46d1ff 100%); 
    -webkit-background-clip: text; 
            background-clip: text;
    color: transparent;
}
